.border-container {
  background-color: #00467f;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  z-index: 1;
}

.top {
  position: absolute;
  left: 0;
  top: 0;
}

.bottom {
  position: absolute;
  left: 0;
  bottom: 0;
}

.header-bg {
  background-image: url(./img/jpeg/header.jpeg);
  width: 100vw;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 0;
}

.logo {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  max-width: 300px;
}

.container {
  display: grid;
  grid-template-columns: 33.33% 33.33% 33.33%;
  grid-template-rows: repeat(2, auto);
  justify-items: center;
  align-items: start;
  justify-content: space-between;
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -30%);
}

.notice {
  grid-column-start: span 3;
  background-color: #00467f;
  border-radius: 25px;
  border: 3px solid white;
  width: 430px;
  min-height: 100px;
  height: auto;
  padding: 0 5%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.notice p {
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-size: 24px;
  color: white;
}

.delivery-badge {
  grid-column-start: span 3;
  background-color: white;
  margin: 5%;
  border-radius: 10px;
  max-width: 250px;
}

.delivery-badge a {
  width: 100%;
}

.delivery-badge a img {
  width: 100%;
}

@media only screen and (max-width: 450px) {
  .logo {
    max-width: 180px;
  }

  .notice {
    width: 85vw;
  }

  .notice p {
    font-size: 18px;
  }
}

@media only screen and (max-width: 350px) {
  .notice p {
    font-size: 16px;
  }
}